<template>
    <div class="container">

      <h1 style="text-align:left"><b>Trámites y Servicios On-Line</b> </h1>
      <div class="rectangulo" ></div>
      <div class="container">
        <div class="form-group col-sm-11"  style="padding-top: 100px; !important">
                        <div class="row">
                          <div class="col-sm-8 input-wrapper">
                              <input type="text" placeholder="Filtrar por nombre" class="buscador col-sm-12">
                          </div>
                          <!-- <div>
                            <i class="fa fa-filter" aria-hidden="true"></i>
                          </div> -->
                          
                        </div>
        </div>
        <div class="form-group col-sm-5" style="padding-right: 0px; !important">
          <ul class="fa-ul">
            <li v-for="tipo of listaTipoTramite" :key=tipo.idTipoTramite  @click="Elegir(tipo.idTipoTramite)" style="text-align:left">
              <a onmouseover="" style="cursor: pointer;"><span class="fa-li"><i class="fa fa-play"></i>
              </span>{{tipo.nombre}}</a> 
            </li>
            
            
          </ul>
        </div>
        <div class="form-group col-sm-5" style="padding-right: 0px; !important">
        </div>
        <div class="form-group col-sm-5" style="padding-right: 0px; !important">
        </div>
        
      </div>
    </div>
    
</template>

<style>
.rectangulo {
     width: 100%; 
     height: 40px; 
     /* border: 3px solid #555; */
     background: #428bca;
}
.buscador {
     border:0;
     border-bottom: 1px solid;
}
.input-wrapper {
    display:inline-block;
    position: relative
}
.input-wrapper:after {
   font-family: 'FontAwesome';
   content: '\f0b0';
   position: absolute;
   right: 6px;
}
</style>
<script>
import axios from 'axios';
export default {
  name: 'TipoTramite',
  props: {
    msg: String
  },
  data(){
    return{
      nombreTipoTramite:'',
      idTipoTramite:0,
      listaTipoTramite:null
    }
  },
  mounted(){
    this.ObtenerTipoTramite(this.nombreTipoTramite);
  },
  methods:{
    reemplazaVacio(cadena){
      return cadena==''?'-':cadena;
    },
    Elegir(idTipoTramite){
      if(idTipoTramite==1)this.$router.push('/tramite/'+idTipoTramite+'&0')
      if(idTipoTramite==2)this.$router.push('/tramite/ce/'+idTipoTramite+'&0')
    },
    ObtenerTipoTramite(nombre){
      //alert('Ingreso');
      //var ruta dev='http://localhost:8929/';  
      var rutaprod="https://mz-services.miraflores.gob.pe:8090/api/tramites/";
      axios.get(rutaprod+'tipotramite/'+this.reemplazaVacio(nombre))
                    .then(response=>{
                      this.listaTipoTramite=response.data.data;
                      console.log(response.data.data);
                      console.log(this.listaTipoTramite);
                      console.log(this.listaTipoTramite[0].nombre);
                  })
                  .catch(e=>console.log(e))
    }
  }
}
</script>


